#product-overview {
  background: url("./images/freedom.jpg") left 10% bottom 20% / cover no-repeat
    border-box;
  /* background-image: url("./freedom.jpg");
  background-size: cover;
  background-position: left 10% bottom 20%; */
  /* background-repeat: no-repeat;
  background-origin: border-box;
  background-clip: border-box; */
  width: 100%;
  height: 528px;
  padding: 10px;
  margin-top: 42px;
  /* border: 5px dashed red; */
}
.section-title {
  color: #2ddf5c;
  text-align: center;
}
#product-overview h1 {
  color: white;
  font-family: "Anton", sans-serif;
}

.plan__list {
  width: 80%;
  margin: auto;
  text-align: center;
}
.plan {
  background: #d5ffdc;
  text-align: center;
  display: inline-block;
  padding: 16px;
  margin: 8px;
  width: 30%;
  vertical-align: middle;
}
.plan--highlighted {
  background: #19b84c;
  color: white;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
}
.plan__annotation {
  background: white;
  color: #19b84c;
  padding: 8px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
}
.plan__title {
  color: #0e4f1f;
}
.plan__price {
  color: #858585;
}
.plan--highlighted .plan__title {
  color: #fff;
}
.plan--highlighted .plan__price {
  color: #0e4f1f;
}
.plan__features {
  list-style: none;
  margin: 0;
  padding: 0;
}
.plan__feature {
  margin: 8px 0;
}

#key-features {
  background: #ff1b68;
  margin-top: 80px;
  padding: 16px;
}
#key-features .section-title {
  color: white;
  margin: 32px;
}
.key-feature__list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.key-feature {
  display: inline-block;
  width: 30%;
  vertical-align: top;
}
.key-feature__image {
  border: 2px solid #424242;
  height: 128px;
  border-radius: 50%;
  background: #ffcede;
  width: 128px;
  margin: auto;
}
.key-feature__description {
  text-align: center;
  font-weight: bold;
  color: white;
  font-size: 20px;
}
